import axios from 'axios'
import { BaseUrl } from '../shared/BaseUrl';
import { myHeaders } from '../shared/MyHeaders';

export async function getAllItem() {
    try {
        const respose = await axios.get(`${BaseUrl}/items?page=0&size=10`
            , { headers: myHeaders })
        return respose.data;
    } catch (error) {
        console.log(error);
    }
}

export async function getAllDataItem() {
    try {
        const respose = await axios.get(`${BaseUrl}/items?page=0&size=10000000`
            , { headers: myHeaders })
        return respose.data;
    } catch (error) {
        console.log(error);
    }
}

export async function deleteDataItem(itemId) {
    try {
        const respose = await axios.delete(`${BaseUrl}/item/${itemId}`
            , { headers: myHeaders })
        return respose;
    } catch (error) {
        console.log(error);
    }
}